/*global kakao*/
import React, { useEffect } from "react";
import styled from "styled-components";

const FlexColumn = styled.div`
  padding-top: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const FlexRow = styled.div`
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
`;

const HotelFont = styled.span`
  font-size: 18px;
  font-weight: bold;
`;

const AdressFont = styled.span`
  font-size: 14px;
  padding-bottom: 20px;
`;

const StylesImage = styled.img`
  padding-right: ${(props) => props.isLeft && "40px"};
  padding-left: ${(props) => !props.isLeft && "40px"};
`;

const TextBlock = styled.div`
  width: 380px;
  font-size: 15px;
  margin-top: 30px;
`;

const Bold = styled.span`
  font-weight: bold;
`;

const Warn = styled.span`
  font-weight: bold;
  color: #ff4747;
`;

function HowToGo() {
  return (
    <>
      <HotelFont>JW    5  </HotelFont>
      <AdressFont>   176 ( 19-3)</AdressFont>
    </>
  );
}

function HowToGoDetails() {
  return (
    <TextBlock>
      <Bold>JW  </Bold> TEL 02-6282-6262{" "}
      <a href={`tel:0262826262`}>
        <img src="/wedding/call.png" alt="call" height={"15px"} />
      </a>
      <br />
      <br />
      <Bold> [3,7,9 ] </Bold> <br />
      3,7,9  7     <br />
      <Bold></Bold>     <br />
      <Bold></Bold>   2 BVLGARI()  {" "}
      <br />
      <Bold></Bold>    3, 7, 9 3   10m <br />
      <Bold></Bold>   <br />
      <br />
      <br />
      <Bold></Bold> <br />
      ,    6    <br />
      <Warn>(  )</Warn> <br />
              .
    </TextBlock>
  );
}
const KakaoMap = styled.div`
  max-width: 780px;
  width: 380px;
  min-width: 200px;
  height: 250px;
  display: block;
`;

const Location = () => {
  useEffect(() => {
    const container = document.getElementById("map");
    const options = {
      center: new kakao.maps.LatLng(37.5041, 127.0048),
      level: 3,
    };

    const map = new kakao.maps.Map(container, options);
    const markerPosition = new kakao.maps.LatLng(37.5041, 127.0048);
    const marker = new kakao.maps.Marker({
      position: markerPosition,
    });
    marker.setMap(map);
  }, []);

  return (
    <>
      <FlexColumn>
        <HowToGo />
        <KakaoMap id="map" />
      </FlexColumn>
      <FlexRow>
        <a href="https://map.kakao.com/link/to/195802694">
          <StylesImage
            src="/wedding/kakao_map.png"
            alt="kakao"
            height={"50px"}
            isLeft
          />
        </a>
        <a
          href={`nmap://route/car?dlat=37.5041&dlng=127.0048&dname=${encodeURI(
            "JW   "
          )}&appname=wedding`}
        >
          <StylesImage
            src="/wedding/naver_map.png"
            alt="naver"
            height={"50px"}
          />
        </a>
      </FlexRow>
      <FlexRow>
        <HowToGoDetails />
      </FlexRow>
    </>
  );
};

export default Location;
